import React, { useState, useEffect } from 'react'
import {
  Row,
  Col,
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Card,
  FormGroup,
  Input,
  Form,
  Button,
  CardHeader,
  CardBody,
  CardTitle,
  Label
} from 'reactstrap'
import Breadcrumbs from '../../components/@vuexy/breadCrumbs/BreadCrumb'
import classnames from 'classnames'
import AddAssets from './AddAssets'
import GoalOverview from '../ui-elements/cards/analytics/GoalOverview'
import { useDropzone } from 'react-dropzone'
import '../../assets/scss/plugins/extensions/dropzone.scss'

let $primary = '#7367F0',
  $success = '#28C76F',
  $danger = '#EA5455',
  $warning = '#FF9F43',
  $primary_light = '#9c8cfc',
  $warning_light = '#FFC085',
  $danger_light = '#f29292',
  $stroke_color = '#b9c3cd',
  $label_color = '#e7eef7'

function ProgrammaticallyDropzone(props) {
  const [files, setFiles] = useState([])
  const { getRootProps, getInputProps, open } = useDropzone({
    //accept: "image/*",
    noClick: true,
    noKeyboard: true,
    onDrop: (acceptedFiles) => {
      setFiles(
        acceptedFiles.map((file) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file)
          })
        )
      )
    }
  })

  const thumbs = files.map((file) => (
    <div className="dz-thumb" key={file.name}>
      <div className="dz-thumb-inner">
        <img src={file.preview} className="dz-img" alt={file.name} />
      </div>
    </div>
  ))

  useEffect(
    () => () => {
      // Make sure to revoke the data uris to avoid memory leaks
      files.forEach((file) => URL.revokeObjectURL(file.preview))
    },
    [files]
  )

  return (
    <section>
      <div {...getRootProps({ className: 'dropzone' })}>
        <input {...getInputProps()} />
        <p className="mx-1" onClick={open}>
          Drag 'n' drop some files here, or click to select files
        </p>
      </div>
      <Button.Ripple color="primary" outline className="my-1" onClick={open}>
        Select File
      </Button.Ripple>
      <aside className="thumb-container">{thumbs}</aside>
    </section>
  )
}

class Assets extends React.Component {
  state = {
    active: '1'
  }

  toggle = (tab) => {
    if (this.state.active !== tab) {
      this.setState({ active: tab })
    }
  }

  render() {
    return (
      <React.Fragment>
        <h2 className="warning spacing">Assets</h2>
        <Row>
          <Col sm="12">
            <TabContent activeTab={this.state.active}>
              <TabPane tabId="1">
                <AddAssets />
              </TabPane>
              <TabPane tabId="2">
                <Card>
                  <CardHeader>
                    <CardTitle>Upload any file, scans, documents</CardTitle>
                  </CardHeader>

                  <CardBody>
                    <Row>
                      <Col md="4" sm="12">
                        <FormGroup className="form-label-group">
                          <Input
                            type="text"
                            name="name"
                            id="nameMulti"
                            placeholder="Attachment Name / Alias"
                          />
                          <Label for="nameMulti">Attachment Name / Alias</Label>
                        </FormGroup>
                      </Col>
                      <Col md="4" sm="12">
                        <FormGroup className="form-label-group">
                          <Input
                            type="text"
                            name="name"
                            id="nameMulti"
                            placeholder="File Name"
                          />
                          <Label for="nameMulti">File Name</Label>
                        </FormGroup>
                      </Col>
                      <Col md="4" sm="12">
                        <FormGroup className="form-label-group">
                          <Input
                            type="Date"
                            name="name"
                            id="nameMulti"
                            placeholder="Expiry Date"
                          />
                          <Label for="nameMulti">Expiry Date</Label>
                        </FormGroup>
                      </Col>
                      <Col md="6" sm="12">
                        <FormGroup className="form-label-group">
                          <Input
                            type="textarea"
                            name="name"
                            id="nameMulti"
                            placeholder="Description"
                          />
                          <Label for="nameMulti">Description</Label>
                        </FormGroup>
                      </Col>
                    </Row>
                    <ProgrammaticallyDropzone />
                  </CardBody>
                </Card>
              </TabPane>
              <TabPane tabId="3">Coming soon .......!</TabPane>
            </TabContent>
          </Col>
        </Row>
      </React.Fragment>
    )
  }
}
export default Assets
